@import url(https://fonts.googleapis.com/earlyaccess/cwtexyen.css);
@import url(https://fonts.googleapis.com/css2?family=DotGothic16&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  width: 100vw;
  height: 100vh;
  background-color: black;
  display: flex;
  align-items: center;
  justify-content: center;
}

.App{color:white;width:100vw;height:100vh;background-color:black;letter-spacing:1px;font-family:"DotGothic16", sans-serif;box-sizing:border-box;display:flex;align-items:center}div,p{box-sizing:border-box;margin:0;padding:0}

.sideBar{width:30%;height:50%;border-right:1px solid white;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:24px}.sideBar div{transition:0.2s all;margin:8px 0;color:#333333;cursor:pointer}.sideBar div:hover{color:white}.sideBar .active{color:white}

.status{height:100%;width:70%;padding:64px;overflow-y:scroll}.basicInfo{display:flex}.basicInfo .photo{height:150px;width:150px;margin-right:16px;background-image:url(data:image/gif;base64,);background-repeat:no-repeat;background-size:cover}.basicInfo .textContainer{width:calc(100% - 200px)}.basicInfo .textContainer p{padding-bottom:8px;display:flex;justify-content:space-between}

.card{border:1px solid white;width:100%;padding:16px;padding-top:36px;position:relative;margin-bottom:32px}.card .header{font-size:16px;position:absolute;background-color:white;height:20px;line-height:20px;width:30%;top:-1px;left:-10px;color:black;font-weight:700;-webkit-transform:skewX(-30deg);transform:skewX(-30deg);padding-left:16px}

.skill{display:flex;align-items:center;margin-bottom:8px}.skill .icon{height:40px;width:40px;display:flex;justify-content:center;align-items:center;border:1px solid white;border-radius:5px}.skill .container{display:flex;flex-direction:column;justify-content:center;margin-left:16px}.skill .container .title{font-weight:700;font-size:16px}.skill .container .content{font-size:14px}

